}
/* The side navigation menu */
.left-sidebar a {
  display: block;
  color: black;
  padding: 0.5rem;
  text-decoration: none;
  font-size: 1.5rem;

}
.left-sidebar a.active {
  background-color: #130f40;
  color: white;
}
.left-sidebar a:hover:not(.active) {
  background-color: #130f40;
  color: white;
}
.paginationBttns {
  width: 80%;
  height: 50px;
  margin-top: 2rem;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.paginationBttns a {
  padding: 15px;
  margin: 8px;
  border-radius: 5px;
  border: 2px solid #130f40;
  color: #04aa6d;
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;
}
.paginationBttns a:hover {
  color: white;
  background-color: #04aa6d;
}
.paginationActive a {
  color: white;
  
  background-color: #04aa6d;
}
.btn-card{
  width: 100%;
  outline:none;
  border: 1px solid white;
  font-weight: 400;
  color: white;
 background-color: #130f40;
  font-size: 1.3rem;
}
.img-hover-zoom {
  position: relative;
  margin: auto;
  overflow: hidden;
  }
 
  /* [2] Transition property for smooth transformation of images */
  .img-hover-zoom img {
    max-width: 100%;
    transition: all 0.3s;
    display: block;
    width: 100%;
    height: auto;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
 
  /* [3] Finally, transforming the image when container gets hovered */
  .img-hover-zoom:hover img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }

